.dark{
    --buttons: rgb(121, 75, 196);
    --butttons_hover: rgb(122, 60, 200);
    --buttons_trans: rgba(121, 75, 196, 0.1);
    --body: rgb(21, 32, 43);
    --text: white;
    --border: rgb(56 68 77);
}

.dark{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: var(--body);
}

.dark h1,h2,h3,h4,p,span,li{
    color: var(--text);
}

.container{
    width: calc(70% - 2rem);
    height: 99%;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    background: transparent;
    padding: 1rem;
}

.button{
    background-color: var(--buttons);
}

@media screen and (max-width: 930px){
    .container{
        width: calc(90% - 2rem);
    }
    .left_bar{
        width: 70px;
        left: 0%;
        padding: 0px;
    }
    .logo{
        position: relative;
        left: -10px;
    }
    .left_bar .button{
        position: relative;
        left: -10px;
    }
    .left_bar .button .icon{
        font-size: 28px;
    }
    .left_bar .button .title{
        display: none;
    }
    .center{
        left: 123px;
        width: 100%;
    }
}

@media screen and (max-width: 504px){
    .container{
        width: calc(100% - 0rem);
        padding: 0rem;
    }
    .left_bar .logo{
        position: relative;
        left: 0px;
    }
    .left_bar .button{
        position: relative;
        border-radius: 0px;
        left: 0px;
    }
    .left_bar .button .icon{
        font-size: 28px;
    }
    .left_bar .button:hover{
        background: transparent;
    }
    .center{
        width: calc(100%) !important;
    }
}