.left_bar{
    position: fixed;
    height: 100%;
    width: 250px;
    border-right: 1px solid var(--border);
    top: 0px;
    overflow: auto;
    overflow-x: hidden;
}

.left_bar .button{
    background: transparent;
    width: fit-content;
    padding: 0.8rem;
    padding-right: 30px;
    border-radius: 30px;
    background: transparent;
    cursor: pointer;
    margin-top: 10px;
}

.left_bar .button:hover{
    background-color: var(--buttons_trans);
}

.left_bar .buttonAtivo .title{
    color: var(--buttons) !important;
}

.left_bar .buttonAtivo .icon{
    color: var(--buttons) !important;
}

.left_bar .button .title{
    margin-left: 20px;
    color: var(--text);
    font-weight: bold;
    font-size: 20px;
}

.left_bar .button .icon{
    color: var(--text);
    font-size: 20px;
    margin-left: 10px;
}

.left_bar .logo{
    font-size: 150%;
    margin-top: -5px;
    margin: 20px;
    margin-left: 15px;
    background: transparent;
    width: fit-content;
    padding: 5px;
    border-radius: 50%;
    width: 35px;
    text-align: center;
    height: 35px;
    cursor: pointer;
}

.left_bar .logo:hover{
    background-color: var(--buttons_trans);
}

.left_bar .button .newsCount{
    position: absolute;
    margin-top: -10px;
    border-radius: 20%;
    padding: 1px;
    background: rgba(0, 0, 0, 70);
    width: 20px;
    text-align: center;
}

.left_bar .newsCount span{
    color: var(--text);
    font-size: 12px;
}

.left_bar .buttonAtivo .newsCount span{
    color: var(--buttons);
}

.left_bar .buttonAtivo .newsCount{
    background: #000;
}

.left_bar .moreItem{
    position: fixed;
    margin-top: 0px;
    width: 200px;
    background: var(--body);
    box-shadow: rgba(136, 153, 166, 0.2) 0px 0px 15px, rgba(136, 153, 166, 0.15) 0px 0px 3px 1px;
    border-radius: 4px;
    padding: 10px;
    margin-left: 10px;
}

a{
    text-decoration: none;
}

.left_bar .moreItem li{
    list-style: none;
    padding: 4px;
    cursor: pointer;
    padding: 10px;
}

.left_bar .moreItem li:hover{
    background: rgba(0, 0, 0, .20);
}

.left_bar .moreItem li .title{
    margin-left: 20px;
}

.left_bar .moreItem li .icon{
    margin-left: 10px;
}

.left_bar .logoAtivo{
    color: var(--buttons);
}

.center{
    position: fixed;
    left: 320px;
    width: calc(100% - 40%);
    margin-left: -52px;
    height: 100%;
    top: 0px;
    border-right: 1px solid var(--border);
    overflow-x: hidden;
    overflow-y: auto;
}

::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
::-webkit-scrollbar-track {
    background: var(--button_trans);
}
  
  /* Handle */
::-webkit-scrollbar-thumb {
    background: var(--buttons);
}
  
  /* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: var(--butttons_hover);
}

.center .header{
    width: calc(100% + 10px);
    border-bottom: 1px solid var(--border);
    position: relative;
}

.center .header h1{
    margin: 15px;
    font-size: 18px;
}

.profile .prof_header{
    height: 30vh;
    background: var(--buttons);
}

.profile .prof_header .cover_img{
    width: 100%;
    height: 100%;
    background-image: url('https://scontent.ffln3-1.fna.fbcdn.net/v/t1.0-9/118766790_920389918446798_8659626172736308380_n.jpg?_nc_cat=108&_nc_sid=e3f864&_nc_ohc=bqAvOrTEotoAX9laZ-C&_nc_ht=scontent.ffln3-1.fna&oh=2e64766fc7b3dc603b6fd321cb0867f8&oe=5F76AB67');
    border-bottom: 2px solid var(--border);
    background-size: cover;
    background-position: center;
}

.profile .avatar{
    position: absolute;
    z-index: 100;
    border-radius: 50%;
    width: 140px;
    height: 140px;
    top: 22%;
    left: 50%;
    transform: translate(-50%);
    border: 2px solid var(--border);
}

.profile .avatar img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.avatar .level{
    position: absolute;
    z-index: 10;
    background: var(--buttons);
    color: var(--text);
    border: 1px solid var(--border);
    padding: 10px;
    border-radius: 40px;
    width: 20px;
    height: 20px;
    text-align: center;
    top: 0px;
    cursor: pointer;
}

.splash{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: var(--body);
    top: 0px;
    left: 0px;
}

.splash .logo{
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 300%;
    position: fixed;
    animation: 1s scale infinite;
}

@keyframes scale{
    0%{
        transform: translate(-50%, -50%) scale(0.8);
    }
    100%{
        transform: translate(-50%, -50%) scale(1);
    }
}